@use './variables' as *;

@mixin circles($r, $bg: null, $tc: null) {
  width: $r;
  height: $r;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: $bg;
  color: $tc;
  @include border-radius($r);
}

@mixin border-radius($radius) {
  -webkit-border-radius: $radius !important;
  -moz-border-radius: $radius !important;
  border-radius: $radius !important;
}

@mixin mview-breakpoint($breakpoint) {
  $mediaQuery: map-get($mview-breakpoints, $breakpoint);

  @if ($mediaQuery !=null) {
    @media #{$mediaQuery} {
      @content;
    }
  } @else {
    @media #{$breakpoint} {
      @content;
    }
  }
}

@mixin placeholder {
  &::-webkit-input-placeholder {
    @content;
  }

  &:-moz-placeholder {
    @content;
  }

  &::-moz-placeholder {
    @content;
  }

  &:-ms-input-placeholder {
    @content;
  }
}
