@use '@mview/scss/utilities/functions' as *;
@use '@mview/scss/utilities/mixins' as *;
@use 'sass:color';

:host {
  display: block;
  width: 100%;
  position: absolute;
  bottom: 0;
  z-index: 10;
}

.player-controls-wrapper {
  padding: 16px;
  background: linear-gradient(#0000, #0e0e0ef5);

  .controls-pip {
    cursor: pointer;
    position: absolute;
    right: 20px;
    bottom: 130%;
    background-color: #00000040;
    display: flex;
    padding: 2px 4px;
    justify-content: center;
    align-items: center;
    border-radius: 4px;
    transition: background-color 0.15s;

    svg {
      width: 32px;
      height: 32px;
      fill: #ffffff93;
      transition: fill 0.15s;
    }

    &:hover {
      background-color: #00000070;

      svg {
        fill: #ffffffdc;
      }
    }
  }

  .controls-inner {
    .play-btn,
    .rewind,
    .forward,
    .volume,
    .speed,
    .fullscreen {
      padding: 2px;
      border-radius: 4px;
      transition: 0.2s;

      &:hover {
        background-color: pick-color('primary', 'blue');
      }
    }

    svg {
      width: 26px;
      height: 26px;
    }

    &.controls-top {
      width: 100%;
      display: flex;
      justify-content: space-between;
      align-items: center;

      .controls-left {
        column-gap: 6px;

        .duration {
          span {
            color: white;
            font-size: 14px;
            line-height: 25px;
          }
        }
      }

      .controls-right {
        column-gap: 16px;

        @keyframes setting-reveal {
          from {
            max-height: 0;
          }

          to {
            max-height: 1000px;
          }
        }

        .volume {
          position: relative;

          &:hover .volume-bar {
            display: flex;
          }

          .volume-bar {
            --volume-bar-display: none;

            position: absolute;
            left: 50%;
            bottom: 90%;
            width: 100%;
            transform: translate(-50%);
            display: var(--volume-bar-display);
            justify-content: center;
            align-items: center;
            padding-bottom: 10px;
            background-color: transparent;
            transition: opacity 0.3s, visibility 0.3s;

            .volume-input {
              --volume-bar-progress: 0;

              display: flex;
              justify-content: center;
              align-items: center;
              position: relative;
              background-color: #ffffff75;
              height: 120px;
              width: 8px;
              border-radius: 20px;

              &::before {
                content: '';
                position: absolute;
                z-index: 10;
                bottom: 0;
                width: inherit;
                height: var(--volume-bar-progress);
                background-color: white;
                border-radius: 20px;
              }

              &::after {
                content: '';
                @include circles(13px, white);
                position: absolute;
                z-index: 11;
                bottom: var(--volume-bar-progress);
                transform: translateY(50%);
                cursor: pointer;
              }
            }
          }
        }

        .speed {
          position: relative;

          &:hover {
            .speed-inside-wrapper {
              display: block;
            }
          }

          .speed-inside-wrapper {
            display: none;
            background-color: white;
            position: absolute;
            bottom: 105%;
            right: 0;
            border-radius: 6px;
            animation: setting-reveal 0.6s cubic-bezier(0.4, 0, 0.2, 1) forwards;
            overflow: hidden;

            &:hover {
              display: block;
            }

            h6 {
              padding: 8px 16px;
              line-height: 20px;
              color: pick-color('primary', 'charcoal');
              border-bottom: 1px solid pick-color('primary', 'fog');
            }

            .option {
              transition: background-color 0.2s ease;
              padding: 8px 16px;
              display: flex;
              align-items: center;
              justify-content: space-between;
              text-align: left;
              color: pick-color('primary', 'charcoal');
              border-radius: 4px;

              &:hover {
                background-color: pick-color('primary', 'blue');
                fill: pick-color('primary', 'blue');

                span {
                  color: white;
                }
              }

              span {
                line-height: 20px;
              }

              &.radio-icon {
                padding: 8px 10px;
                margin-right: 40px;
                position: relative;
                display: flex;

                &::before {
                  content: '';
                  display: block;
                  flex-shrink: 0;
                  margin-right: 10px;
                  transition: all 0.3s ease;
                  @include circles(16px, #0000001a);
                }

                &[aria-checked='true']::before {
                  background-color: pick-color('primary', 'blue');
                }

                &::after {
                  content: '';
                  position: absolute;
                  left: 15px;
                  opacity: 0;
                  top: 50%;
                  transform: translateY(-50%) scale(0);
                  transition: transform 0.3s ease, opacity 0.3s ease;
                  @include circles(6px, #fff);
                }

                &[aria-checked='true']::after {
                  opacity: 1;
                  transform: translateY(-50%) scale(1);
                }
              }
            }
          }
        }
      }
    }

    &.controls-bottom {
      --bar-progress-width: 0;
      margin-top: 8px;

      .progress-bar-wrapper {
        position: relative;
        height: 6px;

        .bar {
          position: absolute;
          top: 0;
          left: 0;
          border-radius: 20px;
          width: 0;
          height: 6px;
        }

        .bar-bg {
          background-color: #ffffff40;
          width: 100%;
          z-index: 5;
        }

        .bar-loaded {
          background-color: #ffffff60;
          z-index: 6;
          pointer-events: none;
        }

        .bar-progress {
          background-color: #ffffff;
          z-index: 7;
          pointer-events: none;
          // width: var(--bar-progress-width);
        }
      }
    }
  }
}
